<template>
    <div class="box">
        <div class="title">人岗匹配</div>
        <div class="content">
            <div class="upload">
                <div class="name">
                    <div class="name1">岗位名称</div>
                    <div class="input">
                        <el-select v-model="workValue" placeholder="历史上传岗位名称" @change="changeWork">
                            <el-option v-for="(item, index) in listWork" :key="index" :label="item.workName"
                                :value="item.workName">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="name2">
                    <div class="name3">岗位职责</div>
                    <div class="input" style="font-size: 14px;">
                        <el-input type="textarea" :rows="6" placeholder="请输入内容" v-model="postDuty" resize="none">
                        </el-input>
                    </div>
                </div>
                <div class="doc">
                    <div class="one">岗位要求</div>
                    <div class="two" style="font-size: 14px;">
                        <el-input type="textarea" :rows="10" placeholder="请输入内容" v-model="postAsk" resize="none">
                        </el-input>
                    </div>
                    <div class="file">
                        <el-upload class="upload-demo" action="``" :auto-upload="auto" :limit="limit"
                            :on-change="fileChange" :headers="{ 'Content-Type': '' }">
                            <el-button size="small" type="primary">点击上传</el-button>
                        </el-upload>
                        <div class="howToUse" @click="centerDialogVisible = true">如何使用？</div>
                    </div>
                    <el-button size="medium" type="primary" class="button" @click="goMatch">开始匹配</el-button>
                </div>
            </div>
            <div class="result">
                <ul>
                    <li style="display: block;" v-for="(item,index) in postResult" :key="index" >
                        <div class="one">{{ item.career }}</div>
                        <div class="two"><span>{{item.name}}|</span><span>{{item.education}}|</span><span>{{ item.age }}</span></div>
                        <div class="three"><span v-if="item.graSchool"> {{item.graSchool}}|</span><span>{{item.profession}}</span></div>
                        <div class="four"><span>工龄 {{ item.workingYears }}</span></div>
                        <div class="five">
                            <el-rate :value="Math.floor(item.total/5)" disabled text-color="#ff9900">
                            </el-rate>
                        </div>
                        <el-button type="primary" class="six" @click="goDe(item.id)">查看详情</el-button>
                    </li>
                </ul>
            </div>
        </div>
        <el-dialog title="如何使用" :visible.sync="centerDialogVisible" width="50%" center>
            <span>上传的岗位文件只能是txt文件并且格式固定,格式如下图所示</span>
            <div style="margin-bottom: 20px;"></div>
            <span><img src="@/assets/如何使用.png"></span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="centerDialogVisible = false">已知晓</el-button>
            </span>
            <div style="margin-bottom: 20px;"></div>
            <div>下载岗位模板
                链接: https://pan.baidu.com/s/1QKwqhdHJ3ays_seN9Cp2kA?pwd=6666  
                提取码:6666</div>
        </el-dialog>
    </div>
</template>

<script>
import { reqUploadWork, reqListWork, reqMatch } from '@/api/index'
import Upload from './Upload.vue'
export default {
    components: { Upload },
    name: 'postMatch',
    data() {
        return {
            workValue: '',
            listWork: [],
            input: '',
            star: 4.5,
            postName: '',
            postDuty: '',
            postAsk: '',
            centerDialogVisible: false,
            auto: true,
            limit: 1,
            selectedFile: null,
            workId: 0,
            resumeList: [],
            postResult:[],
            score:0,
        }
    },
    mounted() {
        this.getListWork()
    },
    methods: {
        changeWork(e) {
            const newList = this.listWork.filter(item => item.workName == e)
            this.postDuty = newList[0].workDuty
            this.postAsk = newList[0].workRequire
        },
        async getListWork() {
            const res = await reqListWork(this.id)
            this.listWork = res.data
        },
        async fileChange(file) {
            this.selectedFile = file
            try {
                const response = await reqUploadWork(this.id, this.selectedFile.raw);
                this.workValue = response.data.workName
                this.postDuty = response.data.workDuty
                this.postAsk = response.data.workRequire
            } catch (error) {
                console.log(error);
            }
        },
        goDe(Id) {
            this.$router.push({ name: 'Analyze', params: { id: Id } })
        },
        async goMatch() {
            const newList = this.listWork.filter(item => item.workName == this.workValue)
            const res = await reqMatch(newList[0].id)
            this.postResult=res.data
        },
    },
    computed: {
        id() {
            return this.$store.state.user.id
        },
    },
}
</script>

<style lang="scss" scoped>
.box {
    height: 1000px;
    width: 100%;

    .title {
        width: 100%;
        height: 100px;
        line-height: 100px;
        text-align: center;
        font-size: 35px;
        font-weight: 700;
        line-height: 150px;
    }

    .content {
        height: 900px;
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .upload,
        .result {
            height: 800px;
            width: 550px;
            border: 2px solid rgba(229, 229, 229, 0.6);
            border-radius: 1%;
        }

        .upload {
            .name {
                width: 100%;
                height: 120px;

                .name1 {
                    transform: translateY(20px);
                    margin-left: 25px;
                    font-size: 15px;
                }


                .input {
                    width: 35%;
                    height: 40px;
                    line-height: 40px;
                    margin-left: 25px;

                    background-color: rgba(246, 249, 252, 1);
                    transform: translateY(30px);
                    border-radius: 0;
                }
            }

            .name2 {
                width: 100%;
                height: 220px;

                .name3 {
                    transform: translateY(20px);
                    margin-left: 25px;
                    font-size: 15px;

                }

                .input {
                    width: 70%;
                    height: 140px;
                    margin-left: 25px;
                    transform: translateY(30px);
                }
            }

            .doc {
                width: 100%;
                height: 420px;
                position: relative;

                .one {
                    transform: translateY(20px);
                    margin-left: 25px;
                    font-size: 15px;
                }

                .two {
                    width: 80%;
                    height: 200px;
                    margin-left: 25px;
                    transform: translateY(30px);
                    border-radius: 0;
                    background-color: rgba(246, 249, 252, 1);
                }

                .file {
                    position: absolute;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    width: 500px;
                    height: 80px;
                    left: 30px;
                    bottom: 30px;

                    .howToUse {
                        margin-top: 20px;
                        font-size: 15px;
                        color: red;
                        cursor: pointer;
                    }
                }

                .button {
                    scale: 1.15;
                    margin-left: 0px;
                    margin-top: 60px;
                    position: absolute;
                    bottom: -10px;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }

        .result {
            overflow-y: scroll;
            overflow-x: hidden;

            li {
                height: 150px;

                width: 100%;
                margin-bottom: 20px;
                border-bottom: 1px solid rgba(229, 229, 229, 0.6);
                border-top: 1px solid rgba(229, 229, 229, 0.6);

                .one {
                    font-size: 20px;
                    padding-top: 20px;
                    margin-left: 20px;
                    margin-bottom: 10px;
                }

                .two {
                    font-size: 15px;
                    margin-left: 20px;
                    margin-bottom: 10px;
                }

                .three,
                .four {
                    margin-left: 20px;
                    margin-bottom: 10px;
                }

                .five {
                    position: relative;
                    top: -70%;
                    left: 70%;
                }

                .six {
                    position: relative;
                    top: -50%;
                    left: 72%;
                }
            }
        }
    }
}
</style>